import React,{useState} from 'react'
import {graphql, Link, useStaticQuery} from 'gatsby'
import Img from "gatsby-image"
import LightBox from "../../../../../Global/App/Lightbox/Lightbox.component"
import AdvanceSearchWidgetSidebar from '../../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import styled from 'styled-components'
const ImgContainer = styled.div`
    cursor:pointer;
`
function BannerSingleV4() {
	const [showLightbox, setShowLightbox] = useState(false)
  const [selectedImage, setSelectedImage] = useState(null)

  const handleOpen = i => e => {
    setShowLightbox(true)
    setSelectedImage(i)
  }
  const handleClose = () => {
    setShowLightbox(false)
    setSelectedImage(null)
  }
  const handlePrevRequest = (i, length) => e => {
    setSelectedImage((i - 1 + length) % length)
  }
  const handleNextRequest = (i, length) => e => {
    setSelectedImage((i + 1) % length)
  }
  	const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
		document.querySelector('body').classList.toggle('body_overlay')
		document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
  const data = useStaticQuery(graphql`
		query{
			source: allFile(filter: { absolutePath: { regex:"/singlev4/" } }) {
            edges {
              node {
                childImageSharp {
                  fluid(maxHeight: 500) {
                    ...GatsbyImageSharpFluid
                    presentationWidth
                  }
                }
              }
            }
          }
        }`
	)
	const images = data.source.edges
	const isSSR = typeof window === "undefined";

    return (
		<div className="listing-title-area">
			<div className="container">
				<div className="row">
                    <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div className="sidebar_content_details style3">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
				<div className="row mb30  mt50">
					<div className="col-lg-7 col-xl-8">
						<div className="single_property_title mt30-767">
							<h2>Luxury Family Home</h2>
							<p>1421 San Pedro St, Los Angeles, CA 90015</p>
						</div>
						<div className="dn db-991">
							<div id="main2">
								<span id="open2" className="flaticon-filter-results-button filter_open_btn style3" onClick={handleSidebar}> Show Filter</span>
							</div>
						</div>
					</div>
					<div className="col-lg-5 col-xl-4">
						<div className="single_property_social_share">
							<div className="price float-left fn-400">
								<h2>$13,000<small>/mo</small></h2>
							</div>
							<div className="spss style2 mt20 text-right tal-400">
								<ul className="mb0">
									<li className="list-inline-item"><Link to="#"><span className="flaticon-transfer-1"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-heart"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-share"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-printer"></span></Link></li>
								</ul>
							</div>
						</div>
					</div>
				</div>
				{!isSSR && (
				<div className="row">
				{showLightbox && selectedImage !== null && (
					<LightBox
						images={images}
						handleClose={handleClose}
						handleNextRequest={handleNextRequest}
						handlePrevRequest={handlePrevRequest}
						selectedImage={selectedImage}
					/>
					)}
					<div className="col-sm-7 col-lg-8">
					<div className="row">
						<div className="col-lg-12">
								<ImgContainer className="spls_style_two mb30-520" onClick={handleOpen(16)} >
								<Img
									fluid={images[16].node.childImageSharp.fluid}  
									className="img-fluid w100"      
									/>
								</ImgContainer>
							</div>
						</div>
					</div>
					<div className="col-sm-5 col-lg-4">

						<div className="row">
							<div className="col-sm-6 col-lg-6">
								<ImgContainer className="spls_style_two mb30" onClick={handleOpen(16)} >
								<Img
								fluid={images[16].node.childImageSharp.fluid}  
								className="img-fluid w100"      
								/>
								</ImgContainer>
							</div>
							<div className="col-sm-6 col-lg-6">
								<ImgContainer className="spls_style_two mb30" onClick={handleOpen(17)} >
								<Img
								fluid={images[17].node.childImageSharp.fluid}  
								className="img-fluid w100"      
								/>
								</ImgContainer>
								
							</div>
							<div className="col-sm-6 col-lg-6">
							<ImgContainer className="spls_style_two mb30" onClick={handleOpen(18)} >
								<Img
								fluid={images[18].node.childImageSharp.fluid}  
								className="img-fluid w100"      
								/>
								</ImgContainer>
								
							</div>
							<div className="col-sm-6 col-lg-6">
							<ImgContainer className="spls_style_two mb30" onClick={handleOpen(19)} >
								<Img
								fluid={images[19].node.childImageSharp.fluid}  
								className="img-fluid w100"      
								/>
								</ImgContainer>
							</div>
							<div className="col-sm-6 col-lg-6">
							<ImgContainer className="spls_style_two mb30" onClick={handleOpen(20)} >
								<Img
								fluid={images[20].node.childImageSharp.fluid}  
								className="img-fluid w100"      
								/>
								</ImgContainer>
							</div>
							<div className="col-sm-6 col-lg-6">
								<ImgContainer className="spls_style_two mb30" onClick={handleOpen(21)} >
								<Img
								fluid={images[21].node.childImageSharp.fluid}  
								className="img-fluid w100"      
								/>
								<div className="overlay popup-img">
										<h3 className="title">+20</h3>
								</div>
								</ImgContainer>
								
							</div>
						</div>
					</div>
				</div>
				)}
			</div>
		</div>
)
}

export default BannerSingleV4
